
(function(c){var d=document,a='appendChild',i='styleSheet',s=d.createElement('style');s.type='text/css';d.getElementsByTagName('head')[0][a](s);s[i]?s[i].cssText=c:s[a](d.createTextNode(c));})
('/*\nCopyright DEPP © 2017 - Ministère de l\'éducation nationale \n*/\n\n\n/*add some style, need to scope (using the typeIdentifier as class prefix) every style declaration to prevent collision */\n\n.trainInteraction {\n    font-size: unset;\n    text-align: left;\n    background: #fff;\n    font-weight: 100;\n    color: black;\n}\n\n.qti-customInteraction .trainvache .trainansw {\n    display: none;\n}\n\n.qti-customInteraction .trainvache .container {\n    border: 1px grey solid;\n    width: 790px;\n    overflow: hidden;\n    float: left;\n}\n\n.qti-customInteraction .trainvache .coccifix {\n    position: absolute;\n    margin-top: 187px;\n    left: 185px;\n}\n\n.qti-customInteraction .trainvache .bg {\n    height: 445px;\n}\n\n\n/* Rotation des roues */\n\n@-webkit-keyframes train-rotating {\n    from {\n        -webkit-transform: rotate(0deg);\n    }\n    to {\n        -webkit-transform: rotate(360deg);\n    }\n}\n\n@-moz-keyframes train-rotating {\n    from {\n        -moz-transform: rotate(0deg);\n    }\n    to {\n        -moz-transform: rotate(360deg);\n    }\n}\n\n.qti-customInteraction .trainvache .roue {\n    position: absolute;\n    margin-top: 130px;\n    left: 129px;\n}\n\n.qti-customInteraction .trainvache .rouego {\n    -webkit-animation: train-rotating 3s linear infinite;\n    -moz-animation: train-rotating 3s linear infinite;\n    transform-origin: 50%;\n}\n\n.qti-customInteraction .trainvache .train {\n    position: absolute;\n    margin-top: 5px;\n}\n\n.qti-customInteraction .trainvache .convoi {\n    margin-top: -447px;\n}\n\n\n/* Translation du train */\n\n@keyframes train-gotrain {\n    from {\n        transform: translate(0px);\n    }\n    to {\n        transform: translate(1000px);\n    }\n}\n\n.qti-customInteraction .trainvache .convoigo {\n    animation: train-gotrain 15s linear;\n    -webkit-animation-fill-mode: forwards;\n}\n\n\n/* Translation de la vache */\n\n@keyframes train-govache {\n    from {\n        transform: translate(0px);\n    }\n    to {\n        transform: translate(-800px);\n    }\n}\n\n.qti-customInteraction .trainvache .vachego {\n    animation: train-govache 15s linear;\n    -webkit-animation-fill-mode: forwards;\n}\n\n.qti-customInteraction .trainvache .rmzero {\n    display: none;\n}\n\n.qti-customInteraction .trainvache .vtraj {\n    display: none;\n}\n\n.qti-customInteraction .trainvache .cctraj {\n    display: none;\n}\n\n.qti-customInteraction .trainvache .ptvvache {\n    display: none;\n    opacity: 0;\n    margin-top: -71px;\n}\n\n.qti-customInteraction .trainvache .vachelook {\n    width: 130px;\n}\n\n.qti-customInteraction .trainvache .ptvcc {\n    display: none;\n    opacity: 0;\n    width: 110px;\n    margin-top: 120px;\n    margin-left: 138px;\n}\n\n.qti-customInteraction .trainvache .cocci {\n    width: 30px;\n    display: none;\n    position: absolute;\n}\n\n.qti-customInteraction .trainvache .cocciblue {\n    width: 30px;\n    display: none;\n    position: absolute;\n    /*  margin-top: 164px; */\n    margin-left: 175px;\n}\n\n.qti-customInteraction .trainvache .coccired {\n    width: 30px;\n    display: none;\n    position: absolute;\n    /*  margin-top: 164px; */\n    margin-left: 220px;\n}\n\n.qti-customInteraction .trainvache .premplan {\n    background-color: #fff;\n    background-size: 100% 100%;\n    background-position: 0px 0px;\n    height: 445px;\n    background-image: url(\'data:image/png;base64,\')!important;\n}\n\n.qti-customInteraction .trainvache .commande {\n    float: left;\n}\n\n.qti-customInteraction .trainvache .coccitab {\n    margin: 0px;\n}\n\n.qti-customInteraction .trainvache td {\n    border-collapse: collapse;\n    text-align: center;\n    border: 1px grey solid;\n    padding: 10px;\n}\n\n.qti-customInteraction .trainvache .bellescoccis {\n    width: 132px;\n    cursor: pointer!important;\n}\n\n.qti-customInteraction .trainvache .macrococci {\n    width: 75px;\n    display: block;\n    margin-left: 29px;\n}\n\n.qti-customInteraction .trainvache .btlat {\n    border-radius: 10px;\n    cursor: pointer!important;\n    padding: 10px;\n    text-align: center;\n    background-color: #ffcc00;\n    margin: 2px;\n    border: 1px grey solid;\n}\n\n.qti-customInteraction .trainvache .btlat img {\n    cursor: pointer!important;\n}\n\n.qti-customInteraction .trainvache .btlat:hover {\n    background-color: #ffe254;\n}\n\n.qti-customInteraction .trainvache .cmdlat {\n    border: 1px grey solid;\n    background-color: gainsboro;\n    height: 447px;\n    margin-top: 0px;\n    float: left;\n    width: 174px;\n}\n\n.qti-customInteraction .trainvache .cmdlat div {\n    padding: 10px;\n    margin: 10px;\n}\n\n.qti-customInteraction .trainvache .armzero {\n    display: none;\n}\n\n.qti-customInteraction .trainvache .cache {\n    display: none;\n    top: 8px;\n    position: absolute;\n    width: 200px;\n    height: 170px;\n}\n\n.qti-customInteraction .trainvache .zeroid {\n    width: 130px;\n}\n\n.qti-customInteraction .trainvache .cctrajimg {\n    width: 120px;\n}\n\n.qti-customInteraction .trainvache .armzeroimg {\n    width: 140px;\n}\n\n.qti-customInteraction .trainvache .vtrajimg {\n    width: 140px;\n}\n\n.qti-customInteraction .trainvache .traj {\n    height: 423px;\n}');
